<template>
  <div class="widgetContainer widgetContainer--center login">
    <div class="navigationHeader">
      <div class="navigationHeader__inner">
        <span class="icon-Arrow-big-left cancel" @click="cancel" />
        <p class="title">
          {{ $t('mobileNo_NavTitle') }}
        </p>
      </div>
    </div>
    <div class="widgetContainer__body">
      <el-form
        @keyup.enter.native="submitForm()"
        :model="user"
        :rules="rules"
        ref="user"
        @submit.prevent.native
        class="wise-form">
        <el-form-item prop="phone" :label="$t('mobileNo_Title')">
          <MobileInput
            :value="user.phone"
            :show-country-code-selector="true"
            @input="mobileNumberUpdate"
            :autofocus="true" />
        </el-form-item>
      </el-form>
      <div class="widgetContainer__footer--fixed border-0">
        <div class="width-100 accept-terms">
          <el-checkbox v-model="isTermAccepted" class="accept-terms__checkbox" :label="$t('mobileNo_SmsTerm')" />
        </div>
        <el-button
          data-testid="submit"
          type="primary"
          @click="submitForm"
          :disabled="(!isValid || isDisabled) || !isTermAccepted"
          class="el-button__brand brand width-100">
          {{ $t('next') }}
        </el-button>
      </div>
    </div>
  </div>
</template>
<script>
import Vue from 'vue';
import MobileInput from '@/components/MobileInput';
import { mapActions, mapGetters } from 'vuex';
import { VueReCaptcha } from 'vue-recaptcha-v3';

Vue.use(VueReCaptcha, { siteKey: process.env.VUE_APP_RECAPTCHA_KEY });

export default {
  name: 'Login',
  components: {
    MobileInput
  },
  data() {
    return {
      isValid: false,
      isDisabled: false,
      user: {
        phone: ''
      },
      isTermAccepted: false,
      rules: {
        phone: 'required'
      }
    };
  },
  computed: {
    ...mapGetters('user', ['getUserDetails']),
  },
  mounted() {
    const recaptcha = this.$recaptchaInstance;
    if(recaptcha) {
      recaptcha.showBadge();
    }
  },
  methods: {
    ...mapActions('user', ['login']),
    async loginWithRecaptcha() {
      this.isDisabled = true;
      // (optional) Wait until recaptcha has been loaded.
      await this.$recaptchaLoaded()
      // Execute reCAPTCHA with action "login".
      const recaptchaToken = await this.$recaptcha('login')

      const loader = this.showLoader();
      this.login({mobileNumber: this.user.phone, recaptchaToken})
        .then(
          /**@param {import('@/modules/user/store/state').userState} user */
          () => {
            this.$router.push('/user/loginOtp');
          }
        )
        .catch((e) => {
          this.isDisabled = false;
          console.log(e);
          this.apiErrorMessage(e);
        })
        .finally(() => {
          this.isDisabled = false;
          loader.close();
        });
    },
    submitForm() {
      if (!this.isValid) {
        return true;
      }
      this.loginWithRecaptcha();
    },
    cancel() {
      this.$router.push('/welcome');
    },
    mobileNumberUpdate(v) {
      if(v) {
        const {value, isValid} = v;
        this.user.phone = value;
        this.isValid = isValid;
      } else {
        this.user.phone = '';
        this.isValid = false;
      }
    }
  },
  destroyed() {
    const recaptcha = this.$recaptchaInstance;
    if(recaptcha) {
      recaptcha.hideBadge();
    }
  }
};
</script>
<style lang="scss">
  .widgetContainer {
    &.login {
      .widgetContainer__body {
        padding-top: 30px;
        position: relative;
      }
    }
    .accept-terms {
      margin-bottom: 10px;
      &__checkbox {
        white-space: break-spaces;
        display: flex;
        .el-checkbox__input {
          padding-top: 4px;
        }
      }
    }
  }
</style>